body {
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem ;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body * {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}
.App {
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.header, .lightHeader {
    position: fixed;
    top: 0;
    left: 0;
    height: 70px;
    width: 100%;
    padding: 0 10vw 0 10vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    z-index: 2;
}

.header {
    color: rgb(255, 228, 51);
    background-image: linear-gradient(rgba(25, 25, 25, 1), rgba(25, 25, 25, 0.9), rgba(25, 25, 25, 0.0));
}

.lightHeader {
    color: rgb(25, 25, 25);
    background-image: linear-gradient(rgba(255, 228, 51, 1), rgba(255, 228, 51, 0.9), rgba(255, 228, 51, 0.0));
}

.header-logo, .lightHeader-logo {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 70px;
    width: 70px;
}

.header-logo {
    background-color: rgb(255, 228, 51);
}

.lightHeader-logo {
    background-color: rgb(25, 25, 25);
}

.header-logo:hover img, .lightHeader-logo:hover img {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.the-logo {
    height: 50px;
    width: 50px;
    cursor: pointer;
    transition: 0.4s;
    -webkit-animation: 2s ease-out 0s 1 rotateSun;
            animation: 2s ease-out 0s 1 rotateSun;
}

.header-info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.header-a {
    margin: 0 0 0 5vw;
    color: rgb(177, 177, 177);
    -webkit-animation: 3s ease-out 0s 1 comeFromNone;
            animation: 3s ease-out 0s 1 comeFromNone;
}

.lightHeader-a {
    margin: 0 0 0 5vw;
    color: rgb(25, 25, 25, 0.6);
    -webkit-animation: 3s ease-out 0s 1 comeFromNone;
            animation: 3s ease-out 0s 1 comeFromNone;
}

.header-a b, .lightHeader-a b {
    transition: 0.2s;
}

.header-a:hover > b {
    color: rgb(255, 228, 51);
}

.lightHeader-a:hover > b {
    color: rgb(25, 25, 25, 1);
}





@-webkit-keyframes comeFromNone {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}





@keyframes comeFromNone {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes rotateSun {
    0% {
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
    10% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    20% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    30% {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    40% {
        -webkit-transform: rotate(-22deg);
                transform: rotate(-22deg);
    }
    50% {
        -webkit-transform: rotate(22deg);
                transform: rotate(22deg);
    }
    60% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
    70% {
        -webkit-transform: rotate(8deg);
                transform: rotate(8deg);
    }
    80% {
        -webkit-transform: rotate(-3deg);
                transform: rotate(-3deg);
    }
    90% {
        -webkit-transform: rotate(2deg);
                transform: rotate(2deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes rotateSun {
    0% {
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
    10% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    20% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    30% {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    40% {
        -webkit-transform: rotate(-22deg);
                transform: rotate(-22deg);
    }
    50% {
        -webkit-transform: rotate(22deg);
                transform: rotate(22deg);
    }
    60% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
    70% {
        -webkit-transform: rotate(8deg);
                transform: rotate(8deg);
    }
    80% {
        -webkit-transform: rotate(-3deg);
                transform: rotate(-3deg);
    }
    90% {
        -webkit-transform: rotate(2deg);
                transform: rotate(2deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}
.main, .lightMain {
    width: 100%;
    padding: 20vh 10vw 70px 10vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

.main, .main * {
    background-color: rgb(27, 25, 26);
    color: rgb(255, 228, 51);
}

.lightMain, .lightMain * {
    background-color: rgb(255, 228, 51);
    color: rgb(27, 24, 26);
}

.intro {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.intro-avatar, .lightIntro-avatar {
    width: 40%;
    margin-top: -50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-animation: 1s ease-out 0s 1 comeFromBlur;
