    margin-right: 20px;
}

.intro-bio {
    color: rgb(177, 177, 177);
}

.lightIntro-bio {
    color: rgb(46, 45, 46);
}





.sites {
    position: relative;
    margin-top: 15vh;
    width: 80vw;
    height: 70vh;
    overflow-x: scroll;
    overflow-y: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    transition: 0.2s;
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}

.site-one, .site-two, .site-three, .site-four {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.site-one {
    margin-left: 0;
}

.site-two {
    margin-left: 80vw;
}

.site-three {
    margin-left: 160vw;
}

.site-four {
    margin-left: 240vw;
}

.site-avatar {
    /* width: 40%; */
    position: relative;
    /* height: 100%; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
