}

.site-info-codeview:hover {
    padding-left: 20px;
}

.site-info-codeview:hover > img {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

.site-info-codeview:hover > h3 {
    margin-left: -5px;
}

.lightSite-info-codeview h3 {
    transition: 0.2s;
