}

.site-info-divider {
    background-color: rgb(177, 177, 177);
    height: 2px;
    width: 20px;
}

.lightSite-info-divider {
    background-color: rgb(46, 45, 46);
    height: 2px;
    width: 20px;
}

.site-info h1, i {
    color: rgb(177, 177, 177);
}

.lightSite-info h1, i {
    color: rgb(46, 45, 46);
}

.site-info h1, i {
    margin: 1em 0 1em 0;
}





.dots-div {
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;
    margin-top: 5vh;
}


.dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.dot, .lightDot {
    position: relative;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    margin: 0 10px 0 10px;
    transition: 0.2s;
}

.dot {
    background-color: rgb(255, 228, 51);
}

.lightDot {
    background-color: rgb(27, 25, 26);
}

.dot:hover {
    background-color: rgb(177, 177, 177);
}

.lightDot:hover {
