}






@media screen and (max-width: 900px) {

    .intro-info h1 {
        font-size: 48px;
    }


}






@media screen and (max-width: 768px) {
    
    .intro {
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-top: 0;
    }

    .intro-avatar, .lightIntro-avatar {
        width: 100%;
        position: relative;
    }

    .site-avatar {
        width: 40%;
        /* height: 100%; */
    }

    .intro-info {
        width: 100%;
        margin-left: 0;
        margin-top: 5vh;
    }

    .intro-info h1 {
        margin: 50px 0 50px 0;
    }

    .site-info h3, .site-info i, .lightSite-info h3, .lightSite-info i {    
        font-size: 10px;
    }

}








@media screen and (max-width: 425px) {  

    .sites {
        overflow-y: hidden;
        height: 70vh;
    }

    .site-one, .site-two, .site-three, .site-four {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: space-around;
                justify-content: space-around;
    }

    .site-info, .lightSite-info {
        width: 90%;
    }

    .site-avatar {
        width: 100%;
        /* height: auto; */
    }

    /* .site-avatar img {
        width: auto;
        height: 100%;
    } */

    .site-avatar img {
        width: 50%;
    }

    .site-gotocode, .site-gotoview {
        right: 50px;
    }

}





@-webkit-keyframes comeFromBlur {
    0% {
        -webkit-filter: blur(20px);
                filter: blur(20px);
    }
    100% {
        -webkit-filter: blur(0);
                filter: blur(0);
    }
}





@keyframes comeFromBlur {
    0% {
        -webkit-filter: blur(20px);
                filter: blur(20px);
    }
    100% {
        -webkit-filter: blur(0);
                filter: blur(0);
    }
}

@-webkit-keyframes slideUp {
    0% {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes slideUp {
    0% {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes slideDown {
    0% {
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes slideDown {
    0% {
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes comeFromNone {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes comeFromNone {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fallDown {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    70% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    75% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
    85% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
    90% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
    97% {
        -webkit-transform: rotate(-9deg);
                transform: rotate(-9deg);
    }
    100% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
}

@keyframes fallDown {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    70% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    75% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
    85% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
    90% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
    97% {
        -webkit-transform: rotate(-9deg);
                transform: rotate(-9deg);
    }
    100% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
}
